import { render } from 'react-dom'
import { PureComponent, SyntheticEvent } from 'react'
import cx from 'classnames'
import { DefaultErrorHandler } from '../global/ErrorHandler'

declare var app: any

type TabType = 'visitas' | 'riesgos' | 'cifras' | 'periodos'
type AppState = {
  tab: TabType
}

const APP_TABS = [ 'visitas', 'periodos', 'riesgos', 'cifras' ]

class App extends PureComponent<{}, AppState> {
  constructor(props: any) {
    super(props)

    const defaultTab = window.location.hash.replace(/^#!t-/, '')

    this.state = {
      tab: APP_TABS.includes(defaultTab) ? defaultTab as TabType : 'visitas'
    }
  }

  onChangeTab = (ev: SyntheticEvent, tab: TabType) => {
    ev.preventDefault()

    this.setState({ tab })
    window.location.hash = '#!t-' + tab
  }

  renderTabs() {
    const { tab } = this.state

    return (
      <div className='ui attached tabular menu'>
        {_.map(APP_TABS, (_tab: TabType) => {
          return (
            <a
              href='#'
              key={_tab}
              onClick={(ev) => this.onChangeTab(ev, _tab)}
              className={cx('item tab-nav-item', { active: tab == _tab })}
              style={{ width: '25%' }}
            >
              <span className='tabname'>{_tab == 'periodos' ? 'Periodos (Impuestos)' : _.str.titleize(_tab)}</span>
            </a>
          )
        })}
      </div>
    )
  }

  renderTabpane() {
    switch (this.state.tab) {
      case 'visitas':
        return <TabpaneVisita contents={app.vars.contents.visitas} showClosed={app.vars.filter_settings.showClosed} />
      case 'periodos':
        return <TabpanePeriodo contents={app.vars.contents.periodos} showClosed={app.vars.filter_settings.showClosed} />
      case 'riesgos':
        return <TabpaneRiesgo content={app.vars.contents.riesgos} showClosed={app.vars.filter_settings.showClosed} />
      case 'cifras':
        return <TabpaneCifra content={app.vars.contents.cifras} showClosed={app.vars.filter_settings.showClosed} />
    }
  }

  render() {
    return (
      <div>
        {this.renderTabs()}

        <div className='ui bottom attached tab segment active'>{this.renderTabpane()}</div>
      </div>
    )
  }
}

type TabpaneVisitaState = {
  selected: string
  contents: { [s: string]: any[] }
  showClosed: boolean
}

class TabpaneVisita extends PureComponent<{ contents: any; showClosed: boolean }, TabpaneVisitaState> {
  constructor(props: any) {
    super(props)

    this.state = {
      selected: _.size(app.vars.visitas) == 0 ? '' : (_.first(app.vars.visitas) as any).id,
      showClosed: this.props.showClosed,
      contents: this.props.contents
    }
  }

  onChange = (ev: SyntheticEvent) => {
    this.setState({ selected: (ev.target as any).value })
  }

  onChangeCheckbox = (ev: SyntheticEvent) => {
    this.setState({ showClosed: (ev.target as any).checked })
    app.vars.filter_settings.showClosed = (ev.target as any).checked
  }

  loadContent() {
    const { selected } = this.state

    $.get(url_for(`proyecto/${app.vars.idproyecto}/reportes/revisiones`), { id_visita: selected }).done((res) => {
      this.setState((prevState) => {
        const contents = { ...prevState.contents }
        contents[selected] = res.response.revisiones

        app.vars.contents.visitas = contents

        return { contents }
      })
    })
  }

  renderContent() {
    const { selected, contents } = this.state

    if (!selected) {
      return
    }

    if (typeof contents[selected] == 'undefined') {
      this.loadContent()

      return <AppListLoader />
    }

    return (
      <AppList
        list={contents[selected]}
        showClosed={this.state.showClosed}
        id_visita={selected}
        emptyText='para esta visita'
      />
    )
  }

  render() {
    if (_.size(app.vars.visitas) == 0) {
      return (
        <div className='alert alert-block nocontent'>
          <p>Este proyecto aun tiene visitas registradas.</p>
        </div>
      )
    }

    const { selected } = this.state

    return (
      <div>
        <div className='ui center aligned basic segment sf_admin_filters'>
          <label htmlFor='visitas-select'>Seleccione la visita cuyas Notas de Revisión desea visualizar:</label>
          <select id='visitas-select' value={selected} className='raw-select' onChange={this.onChange}>
            {_.map(app.vars.visitas, (visita: any) => {
              return (
                <option key={visita.id} value={visita.id}>
                  {visita.name}
                </option>
              )
            })}
          </select>
          <div>
            <label className='inline'>
              <input type='checkbox' checked={this.state.showClosed} onChange={this.onChangeCheckbox} />
              ¿Incluir notas revisadas?
            </label>
          </div>
        </div>

        <div className='ui basic segment'>{this.renderContent()}</div>
      </div>
    )
  }
}

