const AppListLoader = () => {
  return <div className='ui active centered inline text loader'>Cargando Notas de Revisión</div>
}

const AppList = (props: { list: any; id_visita: string; emptyText: string; showClosed: boolean }) => {
  const { id_visita, emptyText, showClosed } = props
  let { list } = props

  if (!showClosed) {
    list = _.reduce(
      list,
      (accumulator, rubro: any) => {
        const revisiones = _.reject(rubro.revisiones, (revision: any) => revision.status == 'REVISADO')

        if (_.size(revisiones) > 0) {
          accumulator.push(rubro)
        }

        return accumulator
      },
      []
    )
  }

  if (_.size(list) == 0) {
    return (
      <div className='alert alert-block nocontent'>
        <h3>0 Notas de Revisión</h3>
        <p>Este proyecto no cuentas con Notas de Revisión registrados {emptyText}.</p>
      </div>
    )
  }

  const badge = _.reduce(
    list,
    (accumulator: number, rubro: any) => {
      return accumulator + _.size(rubro.revisiones)
    },
    0
  )

  return (
    <DefaultErrorHandler>
      <div className='container reporte-container-list'>
        {_.map(list, (rubro: any, k) => {
          return (
            <div className='revision_rubro_container' key={k} style={{ position: 'relative' }}>
              <h4 className='title1'>
                {rubro.info.descripcion}
                <span className='badge'>{badge}</span>
              </h4>
              {rubro.info.url && (
                <div style={{ marginLeft: 20, fontSize: 'smaller' }} className='revision_paso_actions'>
                  <a href={url_for(rubro.info.url)} target='_blank'>
                    {rubro.info.urlText}
                  </a>
                </div>
              )}
              {id_visita.substr(0, 1) == 'p' && <div className='watermark-text'>Año Anterior</div>}
              {_.map(rubro.revisiones, (revision: any, jj) => {
                if (!showClosed && revision.status == 'REVISADO') {
                  return null
                }

                return (
                  <div key={jj} className={cx('revision status-' + _.str.dasherize(revision.status.toLowerCase()))}>
                    <h6 style={{ borderBottom: '1px solid #49858cd1' }}>
                      Nota #{revision.name}
                      <span style={{ fontSize: 10, marginLeft: 10 }}>[{revision.status}]</span>
                    </h6>

                    <blockquote>
                      <div style={{ marginLeft: 20 }} dangerouslySetInnerHTML={{ __html: revision.description }} />
                      <div className='author'>
                        &mdash; Preparado por: {revision.Creator.sigla} ({Date.create(revision.created_at).format('{d}/{MM}/{yy}')})
                      </div>

                      {_.size(revision.Comments) > 0 && (
                        <div className='comments'>
                          {_.map(revision.Comments, (comment: any) => {
                            let comentario = comment.comentario
                            if (comment.status == 'COMPLETADO') {
                              comentario = '<small>[COMPLETADO]</small> ' + comentario
                            } else if (comment.status == 'REVISADO') {
                              comentario = '<small>[REVISADO]</small> ' + comentario
                            }

                            return (
                              <blockquote key={comment.id} className={`comment ${comment.status || ''}`}>
                                <div dangerouslySetInnerHTML={{ __html: comentario }} />
                                <div className='author'>
                                  &mdash; {comment.Creator.User.sigla}
                                  ({Date.create(comment.Creator.ts).format('{d}/{MM}/{year}')})
                                </div>
                              </blockquote>
                            )
                          })}
                        </div>
                      )}
                    </blockquote>
                  </div>
                )
              })}
            </div>
          )
        })}

        <div className='ui basic center aligned segment'>
          <form action={url_for2(`proyecto/:id_proyecto/reportes/revisiones`)} target='_blank'>
            <input type='hidden' name='id_visita' value={id_visita} />
            <input type='hidden' name='showClosed' value={showClosed ? 1 : 0} />
            <input type='hidden' name='sf_format' value='pdf' />
            <button className='btn btn-large btn-save' style={{ backgroundColor: '#0b858d' }}>
              Exportar como PDF
            </button>
          </form>
        </div>
      </div>
    </DefaultErrorHandler>
  )
}

$.unblockUI()
render(<App />, document.getElementById('root'))
